import React from 'react';
import { Modal, InputItem, Toast } from 'antd-mobile';
import './style.less';


class Order extends React.PureComponent {
	constructor(props) {
			super(props);
			this.state = {
				visible: false,
				inputValue: '',
				hasBorderColor: false,
				headerTitle: '河南省电子证照查验中心',
				list: [{
					title: '持证者',
					name: '赵明珍'
				}, {
					title: '证件号',
					name: '412924****5123'
				}, {
					title: '证书编码',
					name: '豫A032021002230'
				}, {
					title: '发证日期',
					name: '2021年02月03日'
				}, {
					title: '有效日期',
					name: ' 2021年02月03日至2023年02月02日'
				}, {
					title: '操作类别',
					name: '建筑起重机械司机（施工升降机）'
				}, {
					title: '电子证照状态',
					name: '正常有效'
				}]
			}
	}

	handleShowModal = (e) => {
		e.preventDefault()
		this.setState({
			visible: !this.state.visible
		})
	}

	onPress = () => {
		console.log('ok', this.state.inputValue)
		if (this.hasBorderColor) {
			this.renderOffline()
		} else {
			this.onClose()
		}
	}

	onClose = () => {
    this.setState({
      visible: false,
    });
	}
	
	afterClose = () => {
		console.log('走了吗')
		!this.state.hasBorderColor && this.props.history.push('/list')
	}
	handleInputChange = (val) => {
		this.setState({
			inputValue: val,
			hasBorderColor: false
		}, () => {
			if (this.state.inputValue === '') {
				this.setState({
					hasBorderColor: true
				})
			} else {
				this.setState({
					hasBorderColor: false
				})
			}
		})
	}

	renderOffline() {
		this.setState({
			visible: true
		})
		Toast.offline('Network connection failed !!!', 1);
	}
		
	renderModal () {
		return (
			<Modal
				visible={this.state.visible}
				transparent
				maskClosable={false}
				title="输入用户名"
				footer={[{ text: '提交', onPress: () => { this.onPress() } }]}
				afterClose={this.afterClose}
			>
				<div style={{border: `1px solid ${this.state.hasBorderColor  ? 'red' : 'blue'}`}}>
					<InputItem
						placeholder="用户名"
						labelNumber={5}
						onChange={this.handleInputChange}
						></InputItem>
				</div>
			</Modal>

		)
	}
    render() {
			const { headerTitle, list } = this.state
        return (
            <div className="warp">
              <div className='header'>
								{headerTitle}
							</div>
							<div className='body'>
								<div className='body_header'>
									<div className='body_header_title'>
										建筑施工特种作业操作资格证书
									</div>
									<div className='body_header_doc'>
										河南省住房和城乡建设厅
									</div>
								</div>
								<div className='body_content'>
									{list.map(v => {
										return (
											<div className='body_content_item' key={v.title}>
												<div className='left'>{v.title}</div>
												<div className='right'>{v.name}</div>
											</div>
										)
									})}
								</div>
							</div>
            </div>
        )
    }
}

export default Order